import React from "react";

interface PaginationProps {
  onPrevPage: () => void;
  onNextPage: () => void;
  page: number;
  size: number;
}

export const Pagination = ({ onPrevPage, onNextPage, page, size }: PaginationProps) => {
  return (
    <div className="pagination">
      <button onClick={() => onPrevPage()}>
        &#9664;
      </button>
      <div style={{ fontSize: "22px" }}>
        {page} of {size}
      </div>
      <button onClick={() => onNextPage()}>
        &#9654;
      </button>
    </div>
  );
}