import React from 'react';

interface ExperienceProps {
  year: number;
  experience?: string;
  link?: string;
  place?: string;
}

export const Experience = ({year, experience, link, place}: ExperienceProps) => {
  return (
    <div className="experience">
      <div style={{width: "110px", textAlign: "center", background: "white", borderRadius: "20px", display: "inline-block", color: "#333", fontWeight: "600", padding: "5px 0"}}>{year}</div>
      {
        experience && link && place && (
        <div style={{borderLeft: "4px dotted white", marginLeft: "55px", padding: "15px 0 40px 40px"}}>
          <table >
            <tbody>
              <tr style={{ verticalAlign: "top" }}>
                {/* <td>
                  <div style={{ whiteSpace: "pre", fontWeight: "600" }}>Jan&nbsp;</div>
                </td> */}
                <td>
                  <div style={{ paddingBottom: "0.4rem" }}>{experience} </div>
                  <div style={{ whiteSpace: "pre" }}><div style={{ display: "inline-block", marginRight: "0.4rem" }}>
                    @</div><a href={link} target="_blank">{place}</a>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      )}
    </div >
  );
}
