import React, { useEffect, useState } from 'react';

interface ScrollForMoreProps {
  show: boolean;
}

export const ScrollForMore = ({ show }: ScrollForMoreProps) => {
  return (
    <div
      className='scroll-for-more'
      style={{
        opacity: show ? "1" : "0"
      }}
    >
      Scroll for more
      <div style={{ marginTop: "7px" }}>&#9660;</div>
    </div>
  );
}