import React, { ReactNode } from 'react';

interface SkillProps {
  children: ReactNode;
}

export const Skill = ({ children }: SkillProps) => {
  return (
    <div className="skill">
      <div style={{ display: "inline-block", transform: "scale(0.9)" }}>&#9679;</div>&#8201;{children}
    </div>
  );
}

interface SkillsProps {
  children: ReactNode;
}

export const Skills = ({ children }: SkillsProps) => {
  return (
    <div className="skills">
      {children}
    </div>
  );
}

interface SkillsColumnProps {
  children: ReactNode;
}

export const SkillsColumn = ({ children }: SkillsColumnProps) => {
  return (
    <div className="skills-column">
      {children}
    </div>
  );
}