import React, { CSSProperties, ReactNode } from 'react';

interface ProjectProps {
  children: ReactNode
  page: number;
  current: number;
  title?: string;
  stack?: string[];
  descriptionStyle?: CSSProperties;
}

export const Project = ({children, page, current, title, stack, descriptionStyle} : ProjectProps) => {
  return (
    <div
      className={`project ${page === current ? "active" : ""}`}
      style={{ left: `${(page - current) * 100}%` }}
    >
      {title && <h3>{title}</h3>}
      <div className="project-description" style={descriptionStyle}>{children}</div>
      <div className="stack">
        {stack && stack.map(tool => <span className="tool" key={tool}>
          {tool}{' '}
        </span>)}</div>
    </div >
  );
}
