import React from 'react';
import { useState } from 'react';
import { Page } from '../../common/components/page';
import { Pagination } from '../../common/components/pagination';
import { Project } from "./components/project";

interface PageProjects {
}

export const PageProjects = ({} : PageProjects) => {
  const W = 60;
  const NUM_PROJECTS = 5;
  const [page, setPage] = useState(1);
  let touchstartX = 0;
  let touchendX = 0;

  const nextPage = () => {
    setPage(page % NUM_PROJECTS + 1);
  }

  const prevPage = () => {
    setPage(page - 1 === 0 ? NUM_PROJECTS : page - 1);
  }

  const checkDirection = () => {
    if (touchstartX - touchendX > W) {
      nextPage();
    }

    if (touchendX - touchstartX > W) {
      prevPage();
    }
  }

  const touchstart = (e) => {
    touchstartX = e.changedTouches[0].screenX;
  }

  const touchend = (e) => {
    touchendX = e.changedTouches[0].screenX;
    checkDirection();
  }

  return (
    <Page>
      <div className="work-title">PROJECTS</div>
      <div className="projects-container">
        <div className="projects" onTouchStart={touchstart} onTouchEnd={touchend}>
          <Project page={1} current={page} title="AI/ML data UI" stack={["React", "HTML", "CSS", "Jest", "Zod", "TypeScript", "JavaScript"]}>
            <p>Implemented part of a web application that helps AI/ML engineers visualize and understand high volumes of data as well as manage various aspects of their workflow.</p>
            <p>By visualizing and comparing different metrics, users can make informed decisions.</p>
          </Project>
          <Project page={2} current={page} title="Interactive Audio Guide" stack={["React", "HTML", "CSS", "JavaScript", "ASP.NET"]}>
            <p>Worked on most of the front-end making an interactive audio guide web-app for a museum used by thousands of users.</p>
            <p>Users can create accounts, buy courses, watch animated characters explain content, do quizzes, and unlock extras by visiting different points of the city.</p>
          </Project>
          <Project page={3} current={page} title="Company Virtual Expo" stack={["HTML", "CSS", "JavaScript", "ASP.NET", "SignalR"]}>
            <p>Implemented the UI for a website for the City of Kyoto where local companies can register and create online booths to showcase their products.</p>
            <p>Prospect clients can contact them through a custom implemented messaging system.</p>
          </Project>
          <Project page={4} current={page} title="Employee Management System" stack={["HTML", "CSS", "JavaScript", "ASP.NET"]}>
            <p>Expanded the functionality, improved the UI, and fixed several bugs for the employee management system of a Japanese multinational that manufactures construction equipment.</p>
          </Project>
          <Project page={5} current={page} descriptionStyle={{ display: "flex", flexDirection: "column", height: "100%", justifyContent: "center", alignItems: "center" }}>
            <p>
              More in my <a href="https://www.linkedin.com/in/federico-garcia-garcia/" target="_blank">LinkedIn</a>
            </p>
          </Project>
        </div>
        <Pagination
          onPrevPage={prevPage}
          onNextPage={nextPage}
          page={page}
          size={NUM_PROJECTS}
        />
      </div>
    </Page>
  );
}

