import './app.css';
import React, { useEffect, useState } from 'react';
import { PageWelcome } from './pages/welcome/page-welcome';
import { PageSkills } from './pages/skills/page-skills';
import { PageContact } from './pages/contact/page-contact';
import { PageExperience } from './pages/experience/page-experience';
import { PageProjects } from './pages/projects/page-projects';
import { BackgroundGradient } from './common/components/background/background-gradient';
import { ScrollForMore } from './common/components/scroll-for-more';

interface AppProps {
}

export const App = ({ }: AppProps) => {
  const [show, setShow] = useState(false);
  let timeout: null | ReturnType<typeof setTimeout> = null;

  const onScroll = () => {
    if (timeout) {
      clearTimeout(timeout);
      timeout = null;
    }

    if (show) {
      setShow(false);
    }
  }

  useEffect(() => {
    // In dev mode, React will execute this twice, so we need
    // to make sure it will only be executed once by adding the
    // check if(!timeout)
    if(!timeout) {
      const appScroll = document.getElementById('App-scroll');

      // Only show the text if we are at the top
      if(!appScroll || appScroll.scrollTop === 0) {
        timeout = setTimeout(() => {
          setShow(true);
        }, 4000);
      }
    }
  }, []);

  return (
    <div className='App'>
      <div id='App-scroll' onScroll={onScroll}>
        <BackgroundGradient />
        <PageWelcome />
        <PageSkills />
        <PageExperience />
        <PageProjects />
        <PageContact />
      </div>
      <ScrollForMore show={show} />
    </div>
  );
}